import { Pagination } from "@nextui-org/react";

const PageSwitch = ({ currentPage, totalPages, setCurrentPage, setCats }) => {
    return (
        <>
            <div className="pagination" style={{
                border: "1px solid var(--nextui-colors-border)",
                borderRadius: "50px"
            }}>
                <Pagination
                    total={totalPages}
                    initialPage={currentPage}
                    size="lg"
                    rounded
                    shadow
                    boundaries={2}
                    color="secondary"
                    onChange={
                        (page) => {
                            // console.log("PAGE>>>", page);
                            setCats(null);
                            setCurrentPage(page);
                        }
                    }
                    css={{
                        margin: "0.7rem"
                    }}
                />
            </div>
        </>

    )
}

export default PageSwitch;