import React, { useState, useEffect } from 'react';
import { Grid, Pagination, Container } from '@nextui-org/react';
import loadingSvg from '../icons/loading.svg'
// Custom components and functions
import AdoptalApi from '../api/adoptalBackend';
import CatCard from "./CatCard";
import FilterSidebar from "./FilterSidebar";
import PageSwitch from './PageSwitch';

// main function
const CatList = () => {
    // using state to keep track of all the cats
    const [cats, setCats] = useState();
    const [currentPage, setCurrentPage] = useState();
    const [totalPages, setTotalPages] = useState();

    // grabbing a list of cats
    useEffect(() => {
        getCats("15106", currentPage);
    }, [currentPage])

    async function getCats(zip, page) {
        let res = await AdoptalApi.getAllCats(zip, page);
        setCats(res.animals);
        setTotalPages(res.pagination.total_pages);
        // console.log(res)
    }

    return (<>
        <Container>
            <Grid.Container gap={1} >
                <Grid xs={2}>
                    <FilterSidebar></FilterSidebar>
                </Grid>
                <Grid xs={10}>

                    <Grid.Container gap={1}>
                        {
                            cats
                                ? cats.map(cat =>
                                    <Grid xs={6} sm={4} lg={3} xl={2} key={cat.id}>
                                        <CatCard cat={cat}></CatCard>
                                    </Grid>)

                                : <img src={loadingSvg} alt='loading spinner' style={{ margin: "0 auto" }}></img>
                        }
                    </Grid.Container>


                </Grid>
                <Grid xs={12} justify="center">
                    <PageSwitch totalPages={totalPages} currentPage={1} setCurrentPage={setCurrentPage} setCats={setCats}></PageSwitch>
                </Grid>
            </Grid.Container>
        </Container>



    </>)
}

export default CatList;