import React, { useState, useEffect } from 'react';
import { Card, Col, Row, Button, Text } from "@nextui-org/react";

// Custom components and functions
import AdoptalApi from '../api/adoptalBackend';

const CatCard = ({ cat }) => {
    return (<>
        <Card css={{ w: "300px", h: "300px" }} isPressable isHoverable>
            {/* <Card.Header css={{ position: "absolute", zIndex: 1, top: 5 }}>
                <Col>
                    <Text size={12} weight="bold" transform="uppercase" color="#9E9E9E">
                        Your day your way
                    </Text>
                    <Text h3 color="white">
                        Your checklist for better sleep
                    </Text>
                </Col>
            </Card.Header> */}
            <Card.Body css={{ p: 0 }}>
                <Card.Image
                    src={cat.primary_photo_cropped !== null ? cat.primary_photo_cropped.small : "/no_photo_640.png"}
                    objectFit="cover"
                    width="100%"
                    height="100%"
                    alt="Relaxing app background"
                />
            </Card.Body>
            <Card.Footer
                isBlurred
                css={{
                    position: "absolute",
                    bgBlur: "#0f111466",
                    borderTop: "$borderWeights$light solid $gray800",
                    bottom: 0,
                    zIndex: 1,
                }}
            >
                <Row fluid>
                    <Col>
                        <Row justify="flex-start" align='center'>
                            <Col span={5}>
                                <Text color="white" size={20} b>
                                    {cat.name}
                                </Text>

                            </Col>
                            <Col span={7}>
                                <Row>
                                    <Col>
                                        <Text b color="#e5e5e5" size={13}>
                                            {cat.age} {cat.gender} 
                                        </Text>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>
                                        <Text b color="#e5e5e5" size={13}>
                                            {cat.breeds.primary}
                                        </Text>
                                    </Col>
                                </Row>

                            </Col>
                        </Row>
                    </Col>
                </Row>
            </Card.Footer>
        </Card>
    </>)
}

export default CatCard;